import React, { Component } from "react";
import TextField from "@material-ui/core/TextField";
import File from "../FileComponent";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import Typography from "@material-ui/core/Typography";
import Select from "@material-ui/core/Select";

const styles = theme => ({
  root: {
    display: "flex",
    flexWrap: "wrap"
  },
  formControl: {
    margin: theme.spacing(),
    minWidth: 120
  },
  selectEmpty: {
    marginTop: theme.spacing(2)
  }
});

let values = [];
let feeFull;

class AddProfile extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  state = {
    category: "",
    description: "",
    services: "",
    fees: "",
    payment: []
  };
  handleChange(e) {
    const name = e.target.name;
    const value = e.target.value;

    if (e.target.type === "checkbox") {
      values.push(value);
      this.setState({ [name]: values });
      return;
    }
    if (e.target.name === "feesParam") {
      feeFull = this.state.fees + " " + e.target.value;
      this.setState({ serviceType: feeFull });
      return;
    }
    this.setState({ [name]: value });
  }

  handleSubmit(event) {
    console.log("==================DATA==============", this.state);
    event.preventDefault();
      this.props.addProfile(
        this.state
      )
  }
  render() {
    const { classes } = this.props;
    const { selected, hasError } = this.state;
    return (
      <div className="App">
        <div className="container" style={{ padding: 16 }}>
          <div className="row" style={{ paddingTop: 100 }}>
            <main role="main" className="col-lg-7">
              <Typography variant="h5" align="center" component="h2" gutterBottom>
                Register your profile
              </Typography>
              <form
                className="text-left"
                onSubmit={this.handleSubmit}
                style={{ borderRight: "1px solid #000", paddingRight: 50 }}
              >
                <FormControl className={classes.formControl} error={hasError}>
                  <InputLabel htmlFor="category">Select Category</InputLabel>
                  <Select
                    name="category"
                    value={selected}
                    onChange={this.handleChange}
                    input={<Input id="yoga" />}
                  >
                    <MenuItem value="yoga">Yoga</MenuItem>
                    <MenuItem value="teaching">Teaching</MenuItem>
                    <MenuItem value="swimming">Swimming</MenuItem>
                    <MenuItem value="diabetes">Diabeties</MenuItem>
                  </Select>
                  {hasError && (
                    <FormHelperText>This is required!</FormHelperText>
                  )}
                </FormControl>
                <div className="form-group">
                  <textarea
                    name="description"
                    className="form-control"
                    id="exampleFormControlTextarea1"
                    rows="3"
                    placeholder="Describe your service not more than 200 words"
                    value={this.state.description}
                    onChange={this.handleChange}
                  />
                </div>
                <div className="form-group">
                  <TextField
                    id="outlined-basic"
                    label="Your Services"
                    variant="outlined"
                    name="services"
                    onChange={this.handleChange}
                    className="form-control"
                    value={this.state.services}
                  />
                </div>
                <div className="row">
                  <div className="col-lg-6">
                    <div className="form-group">
                      <TextField
                        id="outlined-basic1"
                        label="Fees"
                        variant="outlined"
                        name="fees"
                        onChange={this.handleChange}
                        className="form-control"
                        value={this.state.fees}
                      />
                    </div>
                  </div>
                  <div className="col-lg-6">
                    <FormControl
                      className={classes.formControl}
                      error={hasError}
                    >
                      <InputLabel htmlFor="feesParam">Select</InputLabel>
                      <Select
                        name="feesParam"
                        value={selected}
                        onChange={this.handleChange}
                        input={<Input id="yoga" />}
                      >
                        <MenuItem value="per session group">
                          Per session group
                        </MenuItem>
                        <MenuItem value="per person">Per person</MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                </div>

                <div className="row">
                  <div className="col-lg-12">
                    <p>Select payment method</p>
                    <div className="checkbox">
                      <label>
                        <input
                          type="checkbox"
                          onChange={this.handleChange}
                          name="payment"
                          value="Paytm"
                        />Paytm
                      </label>
                    </div>
                    <div className="checkbox">
                      <label>
                        <input
                          type="checkbox"
                          onChange={this.handleChange}
                          name="payment"
                          value="Google Pay"
                        />Google Pay
                      </label>
                    </div>
                    <div className="checkbox">
                      <label>
                        <input
                          type="checkbox"
                          onChange={this.handleChange}
                          name="payment"
                          value="Paypal"
                        />Paypal
                      </label>
                    </div>
                    <div className="checkbox">
                      <label>
                        <input
                          type="checkbox"
                          onChange={this.handleChange}
                          name="payment"
                          value="Phone Pe"
                        />Phone Pe
                      </label>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12 submit-button">
                  <button type="submit" className="btn btn-primary">
                    Submit
                  </button>
                </div>
              </form>
            </main>
            <div className="col-lg-5 optional" style={{marginTop: 50}}>
              <h5 className="text-left">Optional</h5>
              <File label="Upload your pics" />
              <File label="Upload your video" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AddProfile.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(AddProfile);
